.category_wrap {
    display: flex;
    flex-direction: row;
}
.Dropdown {
    background-color: white;
    border: 1px solid black;
    height: 40px;
    border-radius: 2rem;
}
.dropdown_memu {
    display: flex;
    flex-direction: column;
}

.recipe > div:nth-of-type(1) > div:nth-of-type(1) {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
    padding: 0 1rem 0.5rem;
}
.recipe > div:nth-of-type(1) {
    /* serchBar */
    margin-bottom: 1rem;
}
.recipe > div:nth-of-type(1) > div:nth-of-type(2) {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    margin-bottom: 2rem;
    background-color: #f5f5f5;
    border-radius: 100px;
}
.recipe > div:nth-of-type(1) > div:nth-of-type(2) input {
    flex: 1;
    background-color: transparent;
    border: none;
    padding: 0 1rem;
    font-size: 1rem;
}
.recipe > div:nth-of-type(1) > div:nth-of-type(2) button {
    border: none;
    outline: none;
    background-color: transparent;
    padding: 0 1rem;
    font-size: 1.2em;
}

.recipe > div:nth-of-type(2) {
    /* category */
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 1rem;
}
.results {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width: 100%;
    gap: 1rem;
}
.results .recipe_card {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 5px;
}
.results .recipe_card p {
    position: absolute;
    bottom: 0;
    border-radius: 0 0 5px 5px;
    left: 0;
    width: 100%;
    background: rgba(255, 255, 255, 0.7);
    font-family: 'pretendard-medium';
    font-size: 18px;
    color: var(--black);
    text-align: left;
    padding: 20px;
    box-sizing: border-box;
}
.results .recipe_card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1);
    transition: 0.5s;
    z-index: -1;
}
.results .recipe_card:hover img {
    transform: scale(1.1);
}
.results .recipe_card a {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 999;
}
.recipe_pagination_wrap {
    width: 100%;
}
.recipe_pagination {
    width: fit-content;
    display: flex;
    gap: 1rem;
    margin: auto;
    margin-top: 2rem;
}
.recipe_pagination button {
    background-color: rgba(0, 0, 0, 0.1);
    color: rgba(0, 0, 0, 0.5);
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
}
.recipe_pagination button.active {
    background-color: #007bff;
    color: white;
}
.category {
    width: 200px;
}
.reset_button {
    position: absolute;
    top: -68px;
    left: 150px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.main_recipe_list {
    position: relative;
}
@media (max-width: 600px) {
    .results {
        display: grid;
        grid-template-columns: 1fr 1fr;
        width: 100%;
        gap: 1rem;
    }
    .category_wrap {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .category {
        width: 100%;
    }
}
