.hd {
  border-bottom: 1px solid var(--lightGray);
  position: sticky;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 90%);
  /* backdrop-filter: blur(5px); */
  color: var(--black);
  box-shadow: 5px 5px 10px rgb(187 187 187 / 10%);
  z-index: 999;
}
.hd h1 {
}
.hd h1 a {
  display: block;
  background-image: url('../../public//img/logo.png');
  background-size: 150px;
  background-position: center;
  background-repeat: no-repeat;
  padding: 37px 69px;
  text-indent: -9999px;
}
.hd > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
}

.ham.on {
  z-index: 999999;
  position: fixed;
  top: 2rem;
  left: 2rem;
}
.ham.on > span:nth-of-type(1) {
  transform: translateY(6px) rotate(-135deg);
}
.ham.on > span:nth-of-type(2) {
  opacity: 0;
}
.ham.on > span:nth-of-type(3) {
  transform: translateY(-6px) rotate(135deg);
}
.ham {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 20px;
  height: 15px;
  cursor: pointer;
}
.ham span {
  background-color: var(--black);
  /* background-color: var(--white); */
  width: 100%;
  height: 3px;
  border-radius: 3px;
  transition: 0.5s;
  opacity: 1;
}
.ham.on span,
.ham:hover span {
  background-color: var(--main-color);
}

.hd .hamMenu.on {
  transform: translateX(0%);
}
.hd .hamMenu {
  position: fixed;
  top: 0;
  left: 0;
  width: 60%;
  height: 100%;
  background-color: var(--white);
  border-right: 1px solid var(--black);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  padding: 10rem;
  text-align: center;
  transform: translateX(-100%);
  transition: 0.5s;
  z-index: 99999;
}
.hd .hamMenu > a {
  border: 1px solid var(--black);
  width: 100%;
  padding: 1.5rem 0;
  font-family: 'pretendard-bold';
  font-size: 3em;
  color: var(--black);
  position: relative;
  background-color: var(--white);
  transition: 0.5s;
}
.hd .hamMenu > a:hover {
  color: var(--white);
  background-color: var(--main-color);
  box-shadow: inset 7px 7px 5px rgba(0, 0, 0, 10%);
}

.gnb a {
  transition: 0.5s;
}
.gnb a:hover {
  color: var(--main-color);
}
.gnb .logoff {
  /* display: none; */
  display: flex;
  gap: 2rem;
}
.gnb .logon {
  /* display: none; */
  position: relative;
}
.gnb .logon .profileImg a {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color: #d9d9d9;
}
.gnb .logon .profileImg a img {
  opacity: 0;
  width: 100%;
}
.gnb .logon .toggleMenu.profile {
  position: absolute;
  top: 60px;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
  background-color: var(--white);
  border: 1px solid var(--black);
  border-radius: 5px;
  padding: 1.5rem 2.5rem;
  white-space: nowrap;
  box-shadow: 0 5px 10px rgb(0 0 0 / 10%);
}

@media (max-width: 1200px) {
  .hd .hamMenu {
    padding: 6rem;
  }
  .hd .hamMenu > a {
    font-size: 2.5em;
  }
}
@media (max-width: 900px) {
  .hd .hamMenu {
    padding: 4rem;
  }
}
@media (max-width: 800px) {
  .hd .hamMenu {
    width: 100%;
  }
}
